const selectcategoryReducer = (state) => state.categories;

export const selectCategoryNotChange = createSelector(
  [selectcategoryReducer], //

  (categoriesSlice) => categoriesSlice.categories //categoriesSlice===state.categories===selectcategoryReducer
);

export const selectCategories = createSelector(
  [selectCategoryNotChange], //
  (
    categories //categories===state.categories.categories===selectCategoryNotChange
  ) =>
    categories.reduce((acc, category) => {
      const { title, items } = category;
      acc[title.toLowerCase()] = items;
      return acc;
    }, {})
);

export const selectCategoriesIsLoading = createSelector(
  [selectcategoryReducer],
  (categoriesSlice) => categoriesSlice.isLoading
);

